import { graphql } from "gatsby";
import React from "react";
import { SEO } from "../components/misc/seo";
import { Layout } from "../components/structure/layout";
import { BlogArticles } from "../components/widgets/blog-articles";
const BlogArticlesTemplate = ({ pageContext: { currentPage, numPages }, data: { allMarkdownRemark }, }) => {
    return (<Layout>
      <SEO title="Blog Articles"/>
      <BlogArticles currentPage={currentPage} data={allMarkdownRemark} totalPages={numPages}/>
    </Layout>);
};
export default BlogArticlesTemplate;
export const pageQuery = graphql `
  query getBlogArticles($skip: Int!, $limit: Int!) {
    allMarkdownRemark(
      limit: $limit
      skip: $skip
      filter: { frontmatter: { path: { glob: "/blog/**/*" } } }
      sort: { fields: [frontmatter___date], order: DESC }
    ) {
      ...BlogArticles
    }
  }
`;
